@import "shared/styles/breakpoints";

.navbar {
	padding: 3px 0;
	:global {
		.logo {
			display: flex;
			align-items: center;
			img {
				width: 50px;
			}
			@include sm-screen {
				justify-content: center;
			}
		}

		.action__buttons {
			display: flex;
			justify-content: space-between;
			& > * {
				margin: 0 10px;
			}
		}
	}
}
